.composeEmailPage {
    padding: 1rem 3rem 3rem 3rem;
    flex: 1;
}

.composeEmailHeader {
    font-weight: 300;
}

.composeEmailForm {
    display: flex;
}

.composeEmailSectionLeft, .composeEmailSectionRight {
    display: flex;
    flex-direction: column;
    margin: 2rem;
}

.composeEmailSectionLeft {
    width: 500px;
}

.allUsersItemsSection {
    margin-top: 1rem;
}

.receiverText {
    margin: 10px 0;
    font-weight: 400;
    color: rgb(0, 102, 128);
}

.receiverName {
    color: lightseagreen;
}


@media screen and (max-width: 1200px) {
    .composeEmailForm {
        flex-direction: column-reverse;
    }
    .composeEmailSectionLeft, .composeEmailSectionRight {
        width: 80%;
    }
}